<template>
<transition name="modal">
    <div class="modal" @click="closeModal">
    <div class="modal__box" @click.stop="">
        <h2 class="modal__box-title">
            {{edit ? 'Изменить заметку' : 'Добавить заметку'}}
        </h2>
        <div class="modal__box-inputs">
            <label>
                <span>Title</span>
                <input type="text" v-model.trim="title">
            </label>
            <label>
                <span>Content</span>
                <textarea v-model.trim="text"></textarea>
            </label>
        </div>
        <div class="modal__box-btns">
            <button class="modal__box-btn del" @click="closeModal">Отмена</button>
            <button v-if="!edit" class="modal__box-btn edit" @click="addNote">Добавить</button>
            <button v-else class="modal__box-btn edit" @click="editNote">Изменить</button>
        </div>
    </div>
  </div>
</transition>
</template>

<script>
export default {
    props: {
        edit: Boolean,
        editedNote: Object,
    },
    data() {
        return {
            title: '',
            text: '',
        }
    },
    methods: {
        closeModal() {
            this.$emit('closeModal')
            this.title = ''
            this.text = ''
        },
        addNote() {
            if(this.title != '' && this.text != '') {
                const note = {
                title: this.title,
                text: this.text,
                date: new Date().toLocaleDateString()
            }
            this.$emit('addNote', note)
            this.title= ''
            this.text = ''
            }
        },
        editNote() {
            if(this.title != '' && this.text != '') {
                const editNote = {
                    id: this.editedNote.id,
                    title: this.title,
                    text: this.text,
                    date: new Date().toLocaleDateString()
                }
                this.$emit('editedNewNote', editNote)
                this.closeModal()
            }
        }
    }
}
</script>

